@import "../styles/mixins/colors";

.card {
  width: 100%;
  height: 340px;
  position: relative;
  overflow: hidden;

  @media (max-width: 900px) {
    height: 225px;
  }

  .card-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
  }

  h3 {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;
    font-size: 18px;
    font-family: Montserrat;
    font-weight: 500;
    color: #fff;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}
