.footer-container {
  width: 97%;
  height: 209px;
  background-color: #000;
  padding-right: 70px;
  padding-left: 70px;
  margin-left: -70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  gap: 28.556px;
  font-size: 24px;

  @media (max-width: 900px) {
    width: 100%;
    gap: 15px;
    font-size: 12px;
    justify-content: center;
  }

  .logo {
    width: auto;
  }

  .footerText {
    display: flex;
    gap: 4px;

    @media (max-width: 900px) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
  p {
    color: white;
    margin-bottom: 29px;

    @media (max-width: 900px) {
      margin-bottom: 0;
      margin-top: 0;
    }
  }

  @media (max-width: 900px) {
    padding-right: 20px;
    padding-left: 20px;
    margin-left: -20px;
  }
}

.margin-top-about {
  margin-top: 8px;
}

.margin-top-detail {
  margin-top: 10px;
}
