.banner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 223px;
  border-radius: 25px;
  margin-bottom: 43px;

  @media (max-width: 900px) {
    height: 111px;
    margin-bottom: 22px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 25px;
  }

  &-shadow {
    position: absolute;
    opacity: 0.30000001192092896;
    background: #000;
    width: 100%;
    height: 223px;
    border-radius: 25px;

    @media (max-width: 900px) {
      height: 111px;
    }
  }

  p {
    position: absolute;
    color: #fff;
    font-size: 48px;
    font-family: montserrat;
    z-index: 1;
    padding: 31px 16px 32px 16px;

    @media (max-width: 900px) {
      font-size: 24px;
    }
  }
}
